<template>
<div class="vertical-center1">
  <div class="inner-block1">
    <div class="vue-tempalte1">
        <span class="title">GAMRIO</span>
        <span class="content">Our mission is to create a safe, unique and fun experience to gamble online. For this reason, our team existing of young professionals,
             whom all know the gaming themed genre the best, worked hard to create this unique and loved gaming themed gambling website</span>
        <router-link class="btn btn-outline-primary getstart" to="/signup">Get start</router-link>     
    </div>
  </div>
</div>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        data() {
            return {
                phone: '',
                password: ''
            }
        },

        computed: {
            ...mapState({
                error: state => state.auth.error,
                loading: state => state.auth.loading
            })
        },
        methods: {
            login () {
               this.$store.dispatch('auth/clearError'); 
               this.$store.dispatch('auth/setLoading'); 
               this.$store.dispatch('auth/goLogin', {phone: this.phone, password: this.password})
               .then((res) => {
                   if(res) {
                       this.$store.dispatch('users/getUser', {id: res})
                       .then((res1) => {
                           if(res1) {
                               this.$router.push("/home").catch(()=>{})
                           }
                       })
                       
                   }
               })
            }
        },

        created() {
            this.$store.dispatch("auth/clearError")
        }
    }
</script>

